.radio_btn_container {
    margin: 0;
    padding: 0; 
    width: 240px;
    height: 60px;
    }

.radio_btn_1 {
margin: 0;
padding: 0;    
width: 110px;
display: inline-block;
margin-top: 5px;    
}

.radio_btn_2 {
    margin: 0;
    padding: 0;    
    width: 110px;   
display: inline-block;
margin-left: 10px;
margin-top: 5px;    
}

.button_sex {
    position: absolute;
    z-index: -1;
    opacity: 0;
    margin: 0 0 0 7px;
    }

.radio__text {
    display: inline-block;
    position: relative;
    padding: 0 0 0 30px;
    cursor: pointer;
    vertical-align: middle;
    }
.radio__text:before {
    content: '';
    position: absolute;
    top: 1px;
    left: 0;
    width: 20px;
    height: 20px;
    box-sizing: border-box;
    border: 1px solid rgba(31, 32, 65, 0.25);
    border-radius: 10px;
    background: #FFF;
  }
  
  .radio input:checked + .radio__text:after {
    content: '';
    position: absolute;
    top: 5px;
    left: 4px;
    width: 12px;
    height: 12px;
    border-radius: 10px;
    background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
    transition: .2s;
  }
  